import React, { Component } from 'react';

class CartItem extends Component {
  render() {
    const cartItem = this.props.item;
    const quantity = this.props.quantity;
    const emptyCart = this.props.emptyCart;
    return (
      <>
        <div className="cart-item flex items-center">
          <img src={cartItem.fullImage} alt="model" />
          <div className="details">
            <p className="text">Lorem ipsum dolor sit amet, consecte adipiscing elit.</p>
            <p className="span">Quantity: {quantity}</p>
            <div className="price flex between">
              <p>{cartItem.priceAfterDiscount} <span>LE</span></p>
              <button className="pointer" onClick={emptyCart}>Remove</button>
            </div>
          </div>
        </div>
        <div className="total">
          <p>Total: {(parseFloat(cartItem.priceAfterDiscount.replace(/[^\d\.]*/g, "")) * quantity).toLocaleString()} LE</p>
          <div className="buttons flex between">
            <button className="review pointer">Review Cart</button>
            <button className="checkout pointer">Complete Checkout</button>
          </div>
        </div>
      </>
    )
  }
}

export default CartItem;